@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,700;1,400&family=Poppins:ital,wght@0,100;0,200;0,400;0,600;0,700;1,300;1,500&display=swap);
.tasks {
  margin-top: 30px;
}

.tasks li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  font-size: 18px;
  color: #282c34;
  cursor: pointer;
  transition: .3s;
}

.tasks li:hover {
  color: #fff;
  background-color: royalblue;
}

.tasks li .edit {
  margin-right: 15px;
  color: #0e6496;
  font-size: 22px;
  cursor: pointer;
}

.tasks li .delete {
  color: #d34309;
  font-size: 22px;
  cursor: pointer;
}

.tasks li div {
  display: flex;
  align-items: center;
}

.form {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form input[type="text"] {
  height: 40px;
  padding: 0 20px;
  font-size: 18px;
  border: 1px solid #888;
  min-width: 70%;

}

.form button[type="submit"] {
  border: none;
  height: 40px;
  width: 40px;
  background-color: royalblue;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  background-color: #61dafa;
  padding: 30px;
  margin: 50px auto;
  max-width: 640px;
  border-radius: 4px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

.main h1 {
  color: #282c34;
  text-align: center;
}


* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: #282c34;
  color: #fff;
}

button {
  cursor: pointer;
}
